%logo-styles {
  font-family: 'Raleway', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.17;
  letter-spacing: 0.03em;
  color: $accentColor;
  @include desktop {
    font-size: 26px;
    line-height: 1.19;
  }
}

%centering {
  display: flex;
  justify-content: center;
}

%align-centering {
  display: flex;
  align-items: center;
}
